.App {

}

.Participant {
  width: 100%;
}

.ListEdit {
  border: 1px solid gray;
}

@media (max-width:480px) {
  .participantList {
    width: 100%;
  }
  .allocationSlist {
    width: 100%;
  }
}

@media (min-width:481px) {
  .participantList {
    width: 50%;
  }
  .allocationSlist {
    width: 50%;
  }
}

.participantList {
  float: left;
}

.allocationsList {
  float: left;
}

.closeBox {
  padding-bottom: 2px;
  vertical-align: middle;
}

.twoPanel {
  overflow: auto;
}

.staleTitle {
  color: gray;
  font-weight: normal;
}

.staleAssignment {
  color: gray;
}

.finalButton {
  text-align: center;
}

.selectedParticipant {
  background-color: #c0c0c0;
}

.assistanceNote {
  color: #428842;
}

.editRow {
  overflow: auto;
}

.editLabel {
  width: 30%;
  float: left;
}

.editFields {
  width: 60%;
  float: left;
}

.editFields * {
  max-width: 100%;
}

.assistanceList {
  display: table;
}

.assistanceItem {
  display: table-row;
}

.assistanceName {
  display: table-cell;
}

.assistanceCheck {
  display: table-cell;
}


